body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	text-align: center;
}

.nav-item {
	margin: 0 1%;
}

ul {
  padding:0;
  list-style:none;
  width: 50%;
}

li{
  font-size: 1.5rem;
  line-height: 2rem;
  border-bottom: 1px dotted #777;
}

.blogger-pic {
	background-color: #777;
	border-radius: 20px;
	border: 1px #777 solid;
}

body {
	background-color: black;
}

.header {
	margin: 65px 0 40px 0;
	width: 100%;
}

h3 {
	background-color: #353A40;
	border: solid 3px #353A40;
	border-radius: 5px;
	color: #ffffff;
}

a {
	color: black;
}

a:hover {
	opacity: .7;
	text-decoration: none;
	color: #ffffff;
}

.kiss-item:hover {
	background-color: #444444;
}

.kiss-item {
	font-size: 75%;
	width: 80%;
}

.main-blurb, .featured-items, .contactform, .adminform, .authform {
	margin: 10px 0;
	width: 100%;
	background-color: #ffffff;
	padding: 20px;
	border: solid 3px #fff;
	border-radius: 5px;
}

td {
	width: 45%;
	padding: 5px;
}

tr {
	height: 10px;
}

footer {
	text-align: center;
	color: #fff;
}

.light {
	color: #fff;
	font-size: 80%;
}

textarea {
	/* margin: 0 0 0 10px; */
	border: 1px solid #f2f2f2;
	border-radius: 20px;
	background-color: #bbbbbb;
  }

	.form-control {
		width: 100%;
	}

  input {
	border: 1px solid #f2f2f2;
  }
  
  .photo-upload {
	  padding: 5% 1%;
	  border: dashed darkgray 3px;
  }

  .select {
	width: 10%;
	margin: 0 -10 0 10%;
  }
  
  .text-input {
	border: 1px solid #f2f2f2;
	background-color: #bbbbbb;
	width: 100%;
	border: 1px solid #f2f2f2;
	border-radius: 10px;
	margin-top: 1%;
	}
	
	.btn {
		background-color: darkgray;
		border: solid 2px lightgray;
		margin: 1% 0;
	}

	.error-msg {
		margin: 2%;
		font-weight: bold;
	}

	.main-search {
		color: #000;
	}
